import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from '@/components/Index.vue';
import List from '@/components/List.vue';
import Detail from '@/components/Detail.vue';
import Insert from '@/components/Insert.vue';
import Update from '@/components/Update.vue';
import Delete from '@/components/Delete.vue';

import InsertReply from '@/components/reply/Insert.vue';
import DetailReply from '@/components/reply/Detail.vue';
import UpdateReply from '@/components/reply/Update.vue';
import DeleteReply from '@/components/reply/Delete.vue';
Vue.use(VueRouter);

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Index,
    },
    {
      path: '/delete',
      component: Delete,
    },
    {
      path: '/detail',
      component: Detail,
    },
    {
      path: '/insert',
      component: Insert,
    },
    {
      path: '/list',
      component: List,
    },
    {
      path: '/update',
      component: Update,
    },

    {
      path: '/insertreply',
      component: InsertReply,
    },
    {
      path: '/detailreply',
      component: DetailReply,
    },
    {
      path: '/updatereply',
      component: UpdateReply,
    },
    {
      path: '/deletereply',
      component: DeleteReply,
    },
  ],
});
