<template>
  <div>
    <table class="table">
      <tr>
        <th>답변제목</th>
        <td>
          <input type="text" id="replyTitle" v-model="replyTitle" />
        </td>
      </tr>
      <tr>
        <th>답변자 아이디</th>
        <td>
          <input type="text" id="replyUserid" v-model="replyUserid" />
        </td>
      </tr>
      <tr>
        <th>답변내용</th>
        <td>
          <textarea
            id="replyContent"
            name="description"
            rows="10"
            style="width:90%"
            v-model="replyContent"
          ></textarea>
        </td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Insert",
  data() {
    // data는 가져올떄 함수형(return으로 가져와야한다.)
    // 목록 데이터 저장을 위한 배열 선언(배열의 이름 : items)
    return {
      replyContent: "",
      replyUserid: "",
      replyTitle: "",
      qnaNo: ""
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.replyUserid && ((msg = "아이디를 입력해주세요"), (err = false));
      err && !this.replyTitle && ((msg = "제목을 입력해주세요"), (err = false));
      err &&
        !this.replyContent &&
        ((msg = "내용을 입력해주세요"), (err = false));
      if (!err) alert(msg);
      else {
        this.createHandler();
      }
    },
    moveList() {
      this.$router.push(`/detail?qnaNo=${this.qnaNo}`);
    },
    createHandler() {
      const params = new URL(document.location).searchParams;
      this.qnaNo = params.get("qnaNo");
      axios
        .post("http://localhost:9999/happyhouse/api/reply", {
          qnaNo: this.qnaNo,
          replyContent: this.replyContent,
          replyUserid: this.replyUserid,
          replyTitle: this.replyTitle
        })
        .then(response => {
          if (response.data === "success") {
            alert("등록이 완료되었습니다.");
          } else {
            alert("등록 처리시 문제가 발생했습니다.");
          }
          this.moveList();
        })
        .catch(error => {
          this.error = error;
        });
    }
  }
};
</script>

