<template>
  <div class="text-center">
    <table class="table table-bordered w-50">
      <tr>
        <th>질문번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>질문일시</th>
        <td>{{ item.qnaDatetime }}</td>
      </tr>
      <tr>
        <th>질문제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>질문내용</th>
        <td>{{ item.qnaContent }}</td>
      </tr>
      <tr>
        <th>질문자아이디</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
    </table>

    <hr />
    <table v-if="replys.length">
      <div v-for="(reply,index) in replys" :key="index">
        <tr>
          <router-link :to="'/detailreply?replyNo=' + reply.replyNo">{{ reply.replyTitle }}</router-link>
        </tr>
        <hr />
      </div>
    </table>

    <table v-else>
      <th>등록된 답변이 없습니다.</th>
    </table>

    <br />
    <div class="text-center">
      <button class="btn btn-primary" @click="moveList">목록</button>
      <button class="btn btn-primary" @click="moveModify">수정</button>
      <button class="btn btn-primary" @click="moveDelete">삭제</button>
    </div>

    <div class="text-center">
      <button class="btn btn-primary" @click="moveInsertReply">답변등록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "Detail",
  data() {
    return {
      item: {},
      replys: [],
      error: ""
    };
  },
  created() {
    //http://localhost:9999/vue/api/board/2
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        // response내부의 data변수를 사용할떄 destructuring
        console.dir(data);
        this.item = data;
        console.dir(this.item.qnaNo);
      })
      .catch(error => {
        console.dir(error);
        this.error = error;
      });
    axios
      .get(`http://localhost:9999/happyhouse/api/reply/${params.get("qnaNo")}`)
      .then(response => {
        this.replys = response.data;
        console.dir(this.replys);
        console.log(this.replys.length);
      })
      .catch(error => {
        console.dir(error);
      });
  },
  methods: {
    getFormatDate(regtime) {
      // moment 라이브러리를 이용하여 날짜 정보를 년.월.일 시:분:초 의 형태로 반환한다.
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    },
    moveInsertReply() {
      this.$router.push(`/insertreply?qnaNo=${this.item.qnaNo}`);
    },
    moveList() {
      this.$router.push("/list");
    },
    moveModify() {
      this.$router.push(`/update?qnaNo=${this.item.qnaNo}`);
    },
    moveDelete() {
      this.$router.push(`/delete?qnaNo=${this.item.qnaNo}`);
    }
  }
};
</script>

<style></style>
