<template>
  <div class="text-center">
    <table class="table table-bordered w-50">
      <tr>
        <th>답변자 아이디</th>
        <td>{{ reply.replyUserid }}</td>
      </tr>
      <tr>
        <th>답변일시</th>
        <td>{{ reply.replyDatetime }}</td>
      </tr>
      <tr>
        <th>답변 내용</th>
        <td>{{ reply.replyContent}}</td>
      </tr>
    </table>
    <br />
    <div class="text-center">
      <button class="btn btn-primary" @click="moveList">목록</button>
      <button class="btn btn-primary" @click="moveModify">수정</button>
      <button class="btn btn-primary" @click="moveDelete">삭제</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "Detail",
  data() {
    return {
      reply: {},
      error: ""
    };
  },
  created() {
    //http://localhost:9999/vue/api/board/2
    const params = new URL(document.location).searchParams;
    axios
      .get(
        `http://localhost:9999/happyhouse/api/reply/detail/${params.get(
          "replyNo"
        )}`
      )
      .then(response => {
        this.reply = response.data;
        console.dir(this.reply);
      })
      .catch(error => {
        console.dir(error);
      });
  },
  methods: {
    getFormatDate(regtime) {
      // moment 라이브러리를 이용하여 날짜 정보를 년.월.일 시:분:초 의 형태로 반환한다.
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    },
    moveList() {
      this.$router.push("/list");
    },
    moveModify() {
      this.$router.push(`/updatereply?replyNo=${this.reply.replyNo}`);
    },
    moveDelete() {
      this.$router.push(
        `/deletereply?replyNo=${this.reply.replyNo}&qnaNo=${this.reply.qnaNo}`
      );
    }
  }
};
</script>

