<template>
  <div>
    <table class="table">
      <tr>
        <th>답변제목</th>
        <td>
          <input type="text" id="replyTitle" v-model="replyTitle" />
        </td>
      </tr>
      <tr>
        <th>답변자 아이디</th>
        <td>
          <input type="text" id="replyUserid" v-model="replyUserid" readonly />
        </td>
      </tr>
      <tr>
        <th>답변내용</th>
        <td>
          <textarea
            id="replyContent"
            name="description"
            rows="10"
            style="width:90%"
            v-model="replyContent"
          ></textarea>
        </td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">수정</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Update",
  data() {
    return {
      replyNo: "",
      qnaNo: "",
      replyTitle: "",
      replyContent: "",
      replyUserid: ""
    };
  },
  methods: {
    moveList() {
      this.$router.push("/list");
    },
    checkHandler() {
      let err = true;
      let msg = "";
      !this.replyTitle && ((msg = "제목을 입력해주세요"), (err = false));
      err &&
        !this.replyContent &&
        ((msg = "내용을 입력해주세요"), (err = false));
      if (!err) alert(msg);
      else this.updateHandler();
    },
    updateHandler() {
      //   const params = new URL(document.location).searchParams;

      axios
        .put(`http://localhost:9999/happyhouse/api/reply`, {
          replyNo: this.replyNo,
          qnaNo: this.qnaNo,
          replyTitle: this.replyTitle,
          replyContent: this.replyContent,
          replyUserid: this.replyUserid
        })
        .then(response => {
          if (response.data === "success") {
            alert("수정이 완료되었습니다.");
          } else {
            alert("수정처리시 문제가 발생하였습니다.");
          }
        })
        .catch(error => {
          this.error = error;
        })
        .finally(() => {
          this.$router.push(`/detail?qnaNo=${this.qnaNo}`);
        });
    }
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios;
    axios
      .get(
        `http://localhost:9999/happyhouse/api/reply/detail/${params.get(
          "replyNo"
        )}`
      )
      .then(({ data }) => {
        console.dir(data);
        this.replyNo = data.replyNo;
        this.qnaNo = data.qnaNo;
        this.replyTitle = data.replyTitle;
        this.replyContent = data.replyContent;
        this.replyUserid = data.replyUserid;
      })
      .catch(error => {
        console.dir(error);
      });
  }
};
</script>

